import axios from 'axios';

const ES_QUERY_URL = 'ws/es/query';
const JSON_HEADER = {
  headers: {
    'Content-Type': 'application/json',
  }
};

export const getFields = async (pattern) => {
  const url = `ws/es/mapping/${pattern}`;
  try {
    const response = await axios.get(url);
    return response.data;
  } catch (e) {
    return [];
  }
};

export const searchTable = async (pattern, searchBody) => {
  const url = `/${pattern}/_search`;
  const query = {
    method: 'post',
    url: url,
    body: JSON.stringify(searchBody)
  };
  try {
    const response = await axios.post('ws/es/query/table', query, JSON_HEADER);
    return response.data;
  } catch (e) {
    return {};
  }
};

export const search = async (pattern, searchBody) => {
  const url = `/${pattern}/_search`;
  const query = {
    method: 'post',
    url: url,
    body: JSON.stringify(searchBody)
  };

  let rows = [];
  try {
    const response = await axios.post(ES_QUERY_URL, query, JSON_HEADER);
    const total = response.data.hits.total;
    const hits = response.data.hits.hits;
    for (let i = 0; i < hits.length; i++) {
      const hit = hits[i];
      const row = hit['_source'];
      // ignore id
      /*
      const id = hit['_id'];
      let rowData = {};
      rowData.id = id;
      for (const key of Object.keys(row)) {
        rowData[key] = row[key];
      }
      */
      rows.push(row);
    }
    return {
      total: total,
      rows: rows
    }
  } catch (e) {
    return {};
  }
};

export const getFilters = async (pattern, fields) => {
  let promises = [];
  for (let i = 0; i < fields.length; i++) {
    switch (fields[i].type) {
      case 'Number':
      case 'Date':
        promises.push(minMax(pattern, fields[i]));
        break;
      case 'Text':
        promises.push(groupByText(pattern, fields[i]));
        break;
      case 'Bool':
        promises.push(groupByBoolean(pattern, fields[i].esName));
        break;
    }
  }
  const result = await Promise.all(promises);
  return result;
}

const minMax = async (pattern, field) => {
  const esField = field.esName;
  const body = `
  {
    "aggs" : {
      "max" : { "max" : { "field" : "${esField}" } },
      "min" : { "min" : { "field" : "${esField}" } }
    }
  }`;
  const url = `/${pattern}/_search?pretty&size=0`;
  const query = {
    method: 'post',
    url: url,
    body: body
  };

  let result = [];
  try {
    const response = await axios.post(ES_QUERY_URL, query, JSON_HEADER);
    const aggr = response.data.aggregations;
    let result = {
      field: esField,
      type: 'Number',
      data: {
        min: aggr.min.value,
        max: aggr.max.value
      }
    };
    if (field.type === 'Date') {
      result = {
        field: esField,
        type: 'Date',
        data: {
          min: new Date(Number(aggr.min.value)),
          max: new Date(Number(aggr.max.value))
        }
      };
    }
    return result;
  } catch (e) {
    return result;
  }
}

const groupByText = async (pattern, field) => {
  let esField = field.esName;
  if (field.esType === 'text') {
    esField += '.keyword';
  }
  const body = `
  {
    "aggs" : {
      "groups" : {
        "terms" : { 
          "field" : "${esField}",
          "size": 100
        }
      }
    }
  }`;
  const url = `/${pattern}/_search?pretty&size=0`;
  const query = {
    method: 'post',
    url: url,
    body: body
  };

  let result = [];
  try {
    const response = await axios.post(ES_QUERY_URL, query, JSON_HEADER);
    const buckets = response.data.aggregations.groups.buckets;
    for (let i = 0; i < buckets.length; i++) {
      result.push({
        key:  buckets[i].key,
        count: buckets[i].doc_count
      })
    }
    return {
      field: field,
      type: 'Text',
      data: result
    }
  } catch (e) {
    return result;
  }
}

const groupByBoolean = async (pattern, field) => {
  const body = `
  {
    "aggs" : {
      "groups" : {
        "terms" : { "field" : "${field}" }
      }
    }
  }`;
  const url = `/${pattern}/_search?pretty&size=0`;
  const query = {
    method: 'post',
    url: url,
    body: body
  };

  let result = [];
  try {
    const response = await axios.post(ES_QUERY_URL, query, JSON_HEADER);
    const buckets = response.data.aggregations.groups.buckets;
    for (let i = 0; i < buckets.length; i++) {
      result.push({
        key:  buckets[i].key_as_string,
        count: buckets[i].doc_count
      })
    }
     return {
      field: field,
      type: 'Bool',
      data: result
    }
  } catch (e) {
    return result;
  }
}

export const groupByTextCount = async (pattern, field, queryBody = null) => {
  let body = {
    "aggs" : {
      "groups" : {
        "terms" : { 
          "field" : ''
        }
      }
    }
  };
  let esField = field.esName;
  if (field.esType === 'text') {
    esField += '.keyword';
  }
  body.aggs.groups.terms.field = esField;
  if (queryBody != null) {
    body["query"] = queryBody;
  }

  const url = `/${pattern}/_search?pretty&size=0`;
  const query = {
    method: 'post',
    url: url,
    body: JSON.stringify(body)
  };

  let result = [];
  try {
    const response = await axios.post(ES_QUERY_URL, query, JSON_HEADER);
    const buckets = response.data.aggregations.groups.buckets;
    for (let i = 0; i < buckets.length; i++) {
      result.push({
        key:  buckets[i].key,
        count: buckets[i].doc_count
      })
    }
    return result;
  } catch (e) {
    return result;
  }
}

export const buildUrlParams = (obj) => {
  let s = [];
  for (let p in obj) {
    if (obj.hasOwnProperty(p)) {
      s.push(encodeURIComponent(p) + "=" + encodeURIComponent(obj[p]));
    }
  }
  return s.join("&");
}

export const rangeQuery = (field, min = null, max = null) => {
  return {
    "range": {
      [field]: {
        "gte": min,
        "lte": max
      }
    }
  };
}

export const rangeGreaterThanQuery = (field, value) => {
  return {
    "range": {
      [field]: {
        "gte": value
      }
    }
  };
}

export const rangeLessThanQuery = (field, value) => {
  return {
    "range": {
      [field]: {
        "lte": value
      }
    }
  };
}

export const termsQuery = (field, values) => {
  return {
    "terms": {
      [field]: values
    }
  };
}

export const termQuery = (field, value) => {
  return {
    "term": {
      [field]: value
    }
  }
}

export const buildQuery = (filter) => {
  if (filter && filter.length != 0) {
    let query = {
      "bool": {
        "filter": []
      }
    };

    // termsQuery('id.keyword', ['user 1']),
    // termQuery('id.keyword', 'user 1'),
    // rangeQuery('qty', 1, 2)

    for (let i = 0; i < filter.length; i++) {
      const field = filter[i].field;
      const operator = filter[i].operator;
      const value = filter[i].value;
      if (field.type === 'Text') {
        let esField = field.esName;
        if (field.esType === 'text') {
          esField += '.keyword';
        }
        if (operator === 'contain') {
          query.bool.filter.push(termQuery(esField, value));
        } else if (operator === 'in') {
          const valueArray = value.split(",");
          query.bool.filter.push(termsQuery(esField, valueArray));
        }
      } else if (field.type === 'Number' || field.type === 'Date') {
        if (operator === '>=') {
          query.bool.filter.push(rangeGreaterThanQuery(field.esName, value));
        } else if (operator === '<=') {
          query.bool.filter.push(rangeLessThanQuery(field.esName, value));
        }
      }
    }
    return query;
  } else {
    return null;
  }
}