<template>
  <div class="number-range-panel">
    <vue-slider v-bind="slider" v-model="slider.value"></vue-slider>
    <div>
      <div style="float: left;">Min: <input type="number" v-model="slider.value[0]" class="num-range-input" /></div>
      <div style="float: right;">Max: <input type="number" v-model="slider.value[1]" class="num-range-input" /></div>
    </div>
  </div>
</template>

<script>
import vueSlider from 'vue-slider-component'

export default {
  name: 'NumberRangeFilter',
  components: {
    vueSlider
  },
  props: {
    min: Number,
    max: Number
  },
  data () {
    return {
      slider: {
        value: [0, 100],
        width: '100%',
        height: 8,
        dotSize: 16,
        min: 0,
        max: 500,
        disabled: false,
        show: true,
        useKeyboard: false,
        tooltip: 'always',
        formatter: '{value}',
        enableCross: false,
        mergeFormatter: '{value1} ~ {value2}',
        bgStyle: {
          backgroundColor: '#fff',
          boxShadow: 'inset 0.5px 0.5px 3px 1px rgba(0,0,0,.36)'
        },
        tooltipStyle: {
          backgroundColor: '#000000',
          borderColor: '#000000'
        },
        processStyle: {
          backgroundColor: '#999'
        }
      }
    }
  },
  created () {
    this.slider.value = [this.min, this.max];
    this.slider.min = this.min;
    this.slider.max = this.max;
  }
}
</script>

<style>
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[type="number"] {
    -moz-appearance: textfield;
}

.num-range-input {
  width: 50px;
  outline: 0;
  padding: 3px 5px;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #1e9ead;
  background: transparent;
  font-size: 16px;
}

.number-range-panel {
  height: 50px;
  background-color: rgba(95, 92, 92, 0.979);
  padding-top: 36px;
  color: black;
}
</style>
