<template>
  <div class="view-page">
    <div class="row">
      <div class="breadcrumb float-left">Dashboard > Edit: {{ dashboard.name }}</div>
      <div class="float-right"><button class="button button1" @click="save">Save</button></div>
    </div>

    <div class="view-col-3 side-panel">
      <div class="side-panel-title">
        <div class="row">
          <div class="float-left tab-name"><i class="fas fa-cog"></i></div>
          <div class="float-right">
            {{ dashboard.width }} x {{ dashboard.height }}
          </div>
        </div>
      </div>
      <div class="side-panel-content">
        <div class="row">
          <div class="float-left side-panel-label">Snap to grid</div>
          <div class="float-right">
            <label class="switch" style="margin-top: 12px; margin-right: -2px;">
              <input type="checkbox" v-model="gridOptions.snapToGrid">
              <span class="slider round"></span>
            </label>
          </div>
        </div>
        <div class="row">
          <div class="float-left side-panel-label">Show gridlines</div>
          <div class="float-right">
            <label class="switch" style="margin-top: 12px; margin-right: -2px;">
              <input type="checkbox" v-model="gridOptions.showGridlines">
              <span class="slider round"></span>
            </label>
          </div>
        </div>
      </div>

      <!-- grid bg -->
      <div class="side-panel-title">
        <div class="row">
          <div class="float-left">Grid</div>
          <div class="float-right">
            <label class="switch">
              <input type="checkbox" v-model="gridStyle.gridBgOn">
              <span class="slider round"></span>
            </label>
          </div>
        </div>
      </div>
      <div class="side-panel-content" v-if="gridStyle.gridBgOn">
        <div class="row">
          <div class="float-left side-panel-label">Background color</div>
          <div class="float-right">
            <ColorInput v-model="gridStyle.gridBgColor"></ColorInput>
          </div>
        </div>
      </div>
      <!-- grid bg -->

      <!-- title -->
      <div class="side-panel-title">
        <div class="row">
          <div class="float-left">Title</div>
          <div class="float-right">
            <label class="switch">
              <input type="checkbox" v-model="gridStyle.titleOn">
              <span class="slider round"></span>
            </label>
          </div>
        </div>
      </div>
      <div class="side-panel-content" v-if="gridStyle.titleOn">
        <div class="row">
          <div class="float-left side-panel-label">Font color</div>
          <div class="float-right">
            <ColorInput v-model="gridStyle.titleFontColor"></ColorInput>
          </div>
        </div>
        <div class="row">
          <div class="float-left side-panel-label">Background color</div>
          <div class="float-right">
            <ColorInput v-model="gridStyle.titleBgColor"></ColorInput>
          </div>
        </div>
      </div>
      <!-- title -->

      <!-- border -->
      <div class="side-panel-title">
        <div class="row">
          <div class="float-left">Border</div>
          <div class="float-right">
            <label class="switch">
              <input type="checkbox" v-model="gridStyle.borderOn">
              <span class="slider round"></span>
            </label>
          </div>
        </div>
      </div>
      <div class="side-panel-content" v-if="gridStyle.borderOn">
        <div class="row">
          <div class="float-left side-panel-label">Color</div>
          <div class="float-right">
            <ColorInput v-model="gridStyle.borderColor"></ColorInput>
          </div>
        </div>
      </div>
      <!-- border -->

      <!-- panel bg -->
      <div class="side-panel-title">
        <div class="row">
          <div class="float-left">Panel</div>
          <div class="float-right">
            <label class="switch">
              <input type="checkbox" v-model="gridStyle.panelBgOn">
              <span class="slider round"></span>
            </label>
          </div>
        </div>
      </div>
      <div class="side-panel-content" v-if="gridStyle.panelBgOn">
        <div class="row">
          <div class="float-left side-panel-label">Background color</div>
          <div class="float-right">
            <ColorInput v-model="gridStyle.panelBgColor"></ColorInput>
          </div>
        </div>
      </div>
      <!-- panel bg -->

      <div class="side-panel-title">
        Visualizations
      </div>
      <div class="side-panel-content">
        <div v-for="v in dashboard.visualizations" :key="v.id">
          {{ v.name }} {{ v.type }} 
        </div>
      </div>

      <div class="side-panel-title">Visualization</div>
      <div class="side-panel-content">
        <button class="button button1" @click="newVisualization">
          Add Visualization
        </button>
      </div>
      <div class="side-panel-title">Filters</div>
      <div class="side-panel-content">
        TODO: get all indices used by visualizations on this dashboard.
        choose fields to filter.
      </div>
    </div>

    <div class="view-content-col-3" style="padding: 15px;">
      <Grid 
        :dashboard="dashboard" 
        :gridStyle="gridStyle"
        :gridOptions="gridOptions" 
        @box-changed="onBoxChanged"
        @edit-visualization="onEdit"
        @remove-visualization="onRemove"
      ></Grid>
    </div>

  </div>
</template>

<script>
import axios from 'axios';
import Grid from '@/components/poli/Grid';
import ColorInput from '@/components/poli/ColorInput';

const DEFAULT_GRID_STYLE = {
  titleOn: false,
  titleFontColor: '#ffffff',
  titleBgColor: '#000000',
  borderOn: false,
  borderColor: '#000000',
  panelBgOn: false,
  panelBgColor: '#ffffff',
  gridBgOn: false,
  gridBgColor: '#ffffff'
}


export default {
  name: "EditDashboard",
  components: {
    Grid,
    ColorInput
  },
  data: () => ({
    dashboard: {
      id: 0,
      name: '',
      width: 1280,
      height: 720,
      visualizations: [],
    },
    gridStyle: DEFAULT_GRID_STYLE,
    gridOptions: {
      snapToGrid: false,
      showGridlines: false,
      readOnly: false
    }
  }),
  created () {
    this.loadDashboard();
  },
  methods: {
    loadDashboard() {
      this.id = this.$route.params.id;
      axios.get('ws/dashboard/' + this.id)
        .then(res => {        
          const d = res.data;  
          this.dashboard = d || {};
          this.gridStyle = d.gridStyle || DEFAULT_GRID_STYLE;
          console.log('editDashboard created', this.dashboard);
        });
    },
    newVisualization() {
      const dashboardId = this.$route.params.id;
      this.$router.push({ path: `/dashboard/${dashboardId}/visualization/0` });
    },
    save() {
      const dashboard = this.dashboard;
      dashboard.gridStyle = this.gridStyle;
      axios.put('ws/dashboard', dashboard)
        .then(res => {
          console.log('dashboard saved');
        });
    },
    onBoxChanged (boxes) {
      console.log('onBoxChanged', boxes);
      this.dashboard.visualizations = boxes;
      console.log('onBoxChanged', this.dashboard.visualizations);
    },
    onEdit(visualizationId) {
      const dashboardId = this.dashboard.id;
      const vid = visualizationId;
      this.$router.push({ path: `/dashboard/${dashboardId}/visualization/${vid}` });
    },
    onRemove(visualizationId) {
      // TODO: pop up to confirm
      axios.delete('ws/visualization/' + visualizationId)
        .then(res => {
          const index = this.dashboard.visualizations.findIndex(id => id === visualizationId);
          this.dashboard.visualizations.splice(index, 1);
        });
    }
  }
}
</script>

