
import axios from 'axios';

export const getAllIndices = async () => {
  try {
    const response = await axios.get('ws/index');
    return response.data;
  } catch (e) {
    return null;
  }
}

export const getIndexByPattern = async (pattern) => {
  const url = `ws/index/pattern/${pattern}`;
  try {
    const response = await axios.get(url);
    return response.data;
  } catch (e) {
    return null;
  }
};

export const getIndex = async (indexId) => {
  const url = `ws/index/${indexId}`;
  try {
    const response =  await axios.get(url);
    return response.data;
  } catch (e) {
    return null;
  }
};

export const deleteIndex = async (indexId) => {
  return await axios.delete('ws/index/' + indexId);
}