<template>
  <div class="view-page">
    <div class="breadcrumb">Edit pattern</div>
    <div class="view-content-full">
      <h3>Pattern: {{ pattern }}</h3>
      <h3>Name</h3>
      <input type="text" class="form-input" v-model="name" placeholder="Name"/>
      <h3>Search</h3>
      <input type="text" v-model="search" placeholder="search" />
      <h3>Fields</h3>
      <table>
        <thead>
          <tr>
            <th>ES NAME</th>
            <th>ES TYPE</th>
            <th>NAME</th>
            <th>TYPE</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="f in fields" :key="f.esName">
            <td>{{ f.esName }}</td>
            <td>{{ f.esType }}</td>
            <td><input type="text" class="side-panel input" v-model="f.name" /></td>
            <td>{{ f.type }}</td>
          </tr>
        </tbody>
      </table>
      <button class="button button1" @click="updateIndex">Save</button>
      
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import * as esApi from '@/api/EsApi';
import * as poliApi from '@/api/PoliApi';

export default {
  name: 'EditIndex',
  data: () => ({
    id: '',
    pattern: '',
    name: '',
    fields: [],
    filteredFields: [],
    search: ''
  }),
  created () {
    this.loadFields();
  },
  computed: {
    filteredFields () {
      return this.fields;
    }
  },
  methods: {
    updateIndex () {
      let index = {};
      index.id = this.id;
      index.name = this.name;
      index.pattern = this.pattern;
      index.fields = this.fields;
      axios.put('ws/index/', index)
        .then(res => {
          this.$router.push({ path: `/index` });
        });
    },
    async loadFields () {
      const id = this.$route.params.id;
      // let index = await poliApi.getIndexByPattern(this.pattern);
      let index = await poliApi.getIndex(id);
      this.id = index.id;
      this.name = index.name;
      this.pattern = index.pattern;
      if (index.fields === undefined || index.fields.length == 0) {
        const fields = await esApi.getFields(this.pattern);
        this.fields = fields;
      } else {
        this.fields = index.fields;
      }
    }
  }
}
</script>

<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.dim {
  background-color: black;
  color: grey
}
</style>
