<template>
  <div class="view-page">
    view dashboard
    <button class="button button1">Share</button>
    <h3>filter Panel - toggle</h3>
    <div>
      TODO: range filters.
    </div>
    <div class="view-content-full">
      <Grid 
        :dashboard="dashboard" 
        :gridOptions="gridOptions" 
      ></Grid>
    </div>
  </div>
</template>

<script>
import Grid from '@/components/poli/Grid';

export default {
  name: 'ViewDashboard',
  components: {
    Grid
  },
  data: () => ({
    dashboard: {},
    gridOptions: {
      snapToGrid: false,
      showGridlines: false,
      readOnly: true
    }
  }),
  created () {
    this.loadDashboard();
  },
  methods: {
    loadDashboard() {
      this.id = this.$route.params.id;
      axios.get('ws/dashboard/' + this.id)
        .then(res => {        
          this.dashboard = res.data;  
          console.log('editDashboard created', this.dashboard);
        });
    },
  }
}
</script>

