<template>
  <div>
    <div style="width: 20px; height: 20px; margin-top: 12px" :style="'background-color: ' + value" @click="showPanel = !showPanel">
    </div>
    <Chrome :value="colors" @input="updateColor" v-if="showPanel"></Chrome>
  </div>
</template>

<script>
import { Chrome } from 'vue-color';

export default {
  name: 'ColorInput',
  components: {
    Chrome
  },
  props: {
    value: String
  },
  data () {
    return  {
      colors: '#123456',
      showPanel: false
    }
  },
  watch: {
    value (newVal) {
      this.colors = newVal;
    }
  },
  methods: {
    updateColor (color) {
      this.$emit('input', color.hex);
    }
  }
}
</script>

<style>
.vc-chrome {
  position: absolute;
  z-index: 10;
  left: 54px;
}
</style>
