import Vue from 'vue';
import VueRouter from 'vue-router';

import System from '@/components/page/System';

import Index from '@/components/page/Index';
import EditIndex from '@/components/page/index/EditIndex';
import ExploreIndex from '@/components/page/index/ExploreIndex';

import Dashboard from '@/components/page/Dashboard';
import EditDashboard from '@/components/page/dashboard/EditDashboard';
import ViewDashboard from '@/components/page/dashboard/ViewDashboard';
import EditVisualization from '@/components/page/dashboard/EditVisualization';

Vue.use(VueRouter);

export default new VueRouter({
  routes: [
    { path: '/', component: System },
    { path: '/system', component: System },
    { path: '/dashboard', component: Dashboard },
    { path: '/dashboard/edit/:id', component: EditDashboard },
    { path: '/dashboard/view/:id', component: ViewDashboard },
    { path: '/dashboard/:dashboardId/visualization/:visualizationId', component: EditVisualization },
    { path: '/index', component: Index },
    { path: '/index/edit/:id', component: EditIndex }, 
    { path: '/index/explore/:id', component: ExploreIndex }
  ]
});
