<template>
  <div class="grid-box" :id="id" :style="boxStyle">
    <div class="grid-box-title" :style="titleStyle" v-if="gridStyle.titleOn">
      {{ visualization.name }}
    </div>
    <div class="grid-box-content" :style="contentStyle">
      <template v-if="visualization.type === 'pie' || visualization.type === 'bar'">
        <v-chart :options="chartOptions" :auto-resize="true" @click="onChartClick"/>
      </template>
      <template v-if="visualization.type === 'table'">
        <table>
          <tr v-for="i in 10" :key="i">
            <td v-for="j in 10" :key="j">
              {{ i }} {{ j }}
            </td>
          </tr>
        </table>
      </template>
    </div>
    <div class="grid-box-resize" v-if="!readOnly"></div>
    <div class="grid-box-edit" v-if="!readOnly">
      <i class="fas fa-edit fa-fw" @click.self="edit"></i>
    </div>
    <div class="grid-box-remove" v-if="!readOnly" >
      <i class="fas fa-times fa-fw" @click.self="remove"></i>
    </div>

    
    
  </div>
</template>

<script>

import * as esApi from '@/api/EsApi';
import * as echartsApi from '@/api/EchartsApi';

import axios from 'axios';

export default {
  name: "GridBox",
  props: {
    visualization: Object,
    readOnly: Boolean,
    gridStyle: Object
  },
  data () {
    return {
      chartOptions: {},
    }
  },
  created() {

    this.chartOptions = {};

    if (this.visualization.type === 'pie') {
      this.createPieChart();
    }
  },
  computed: {
    id () {
      return 'grid-box-' + this.visualization.id;
    },
    boxStyle () {
      const x = this.visualization.x;
      const y = this.visualization.y;
      const width = this.visualization.width;
      const height = this.visualization.height;
      const zIndex = this.zIndex;
    
      let style = `left: ${x}px; top: ${y}px; width: ${width}px; height: ${height}px; z-index: ${zIndex};`;
      if (this.gridStyle.borderOn) {
        const borderColor = this.gridStyle.borderColor;
        style += `border: 3px solid ${borderColor};`;
      }
      return style;
    },
    titleStyle() {
      if (this.gridStyle.titleOn) {
        const color = this.gridStyle.titleFontColor;
        const bgColor = this.gridStyle.titleBgColor;
        let style = `color: ${color}; background-color: ${bgColor};`;
        return style;
      }
    },
    contentStyle () {
      const height = this.gridStyle.titleOn ? this.visualization.height - 26 : this.visualization.height;
      let style = `height: ${height}px; overflow: auto;`;
      if (this.gridStyle.panelBgOn) {
        const bgColor = this.gridStyle.panelBgColor;
        style += `background-color: ${bgColor};`
      }
      return style;
    }
  },
  methods: {
    async createPieChart() {
      console.log(this.visualization);

      const query = esApi.buildQuery(this.visualization.filter);
      const result = await esApi.groupByTextCount(this.visualization.indexPattern, this.visualization.option.key, query);
      
      let legend = [];
      let series = [];
      for (let i = 0; i < result.length; i++) {
        legend.push(result[i].key);
        series.push({
          name: result[i].key,
          value: result[i].count
        });
      }

      this.chartOptions = echartsApi.getPieOptions(legend, series);
      console.log(this.chartOptions);
    },
    edit() {
      this.$emit('edit-visualization', this.visualization.id);
    },
    remove() {
      this.$emit('remove-visualization', this.visualization.id);
    },
    onChartClick(event) {
      console.log(event);
    }
  }
}
</script>

<style>
.grid-box {
  position: absolute;
  width: 100px;
  height: 100px;
  border: 3px solid transparent;
}

.grid-box-title {
  padding: 3px 8px 4px 8px
}

.grid-box-title:hover {
  cursor: grab;
}

.grid-box-resize { 
  width: 12px;
  height: 12px;
  background-color: #4b4f56;
  position:absolute; 
  right: 0; 
  bottom: 0; 
  cursor: move; 
}

.grid-box-remove { 
  height: 18px;
  padding: 3px;
  position:absolute; 
  right: 0; 
  top: 1px; 
}

.grid-box-edit { 
  width: 18px;
  height: 18px; 
  padding: 3px;
  position:absolute; 
  right: 22px; 
  top: 0; 
}
</style>
