<template>
  <div class="grid" :style="style" ref="grid">
    <GridBox 
      v-for="b in boxes" 
      :key="b.id" 
      :visualization="b" 
      :gridStyle="gridStyle" 
      :zIndex="2" 
      :readOnly="gridOptions.readOnly"
      @edit-visualization="onEdit"
      @remove-visualization="onRemove"
    />
  </div>
</template>

<script>
import GridBox from './GridBox';
import axios from 'axios';

export default {
  name: "Grid",
  props: {
    dashboard: Object,
    gridStyle: Object,
    gridOptions: Object,
  },
  components: {
    GridBox
  },
  mounted () {
    this.$refs.grid.addEventListener('mousedown', this.mouseDown, false);
    this.$refs.grid.addEventListener('mouseup', this.mouseUp, false);
    this.$refs.grid.addEventListener('mousemove', this.mouseMove, false);

    const parentNode = this.$el.parentNode;
    const width = parentNode.offsetWidth;
    const height = parentNode.offsetHeight;
    // fit height or width?
    // console.log(parentNode, width, height);
    
  },
  beforeDestroy  () {
    this.$refs.grid.removeEventListener('mousedown', this.mouseDown, false);
    this.$refs.grid.removeEventListener('mouseup', this.mouseUp, false);
    this.$refs.grid.removeEventListener('mousemove', this.mouseMove, false);
  },
  data () {
    return {
      boxes: [],
      dragObject: null,
      isDragging: false,
      isResizing: false,
      xOffset: 0,
      yOffset: 0,
      currentX: 0,
      currentY: 0
    }
  },
  computed: {
    style () {
      const width = this.dashboard.width;
      const height = this.dashboard.height; 

      const gridlines = `
        background-size: 20px 20px;
        background-image: linear-gradient(to right, grey 1px, transparent 1px), linear-gradient(to bottom, grey 1px, transparent 1px);
      `

      let style = `width: ${width}px; height: ${height}px;`;
      if (this.gridOptions.showGridlines) {
        style += gridlines;
      }

      if (this.gridStyle.gridBgOn) {
        const bgColor = this.gridStyle.gridBgColor;
        style += `background-color: ${bgColor};`;
      }

      return style;
    },
  },
  watch: {
    dashboard (newVal) {
      this.boxes = newVal.visualizations;
      console.log('grid', this.boxes);
    }
  },
  methods: {
    mouseDown (event) {
      event.preventDefault();

      if (event.target.className === 'grid-box-title') {
        if (this.dragObject == null) {
          this.dragObject = event.target.parentNode;
          this.isDragging = true;
          this.isResizing = false;

          this.xOffset = event.clientX - this.dragObject.offsetLeft;
          this.yOffset = event.clientY - this.dragObject.offsetTop;
          
          // console.log('down', event.clientX, event.clientY, this.xOffset, this.yOffset);

          var boxes = document.getElementsByClassName("grid-box");
          for (let i = 0; i < boxes.length; i++) {
              boxes[i].style.zIndex = 2;
          }
          this.dragObject.style.zIndex = 3;
        }
      } else if (event.target.className === 'grid-box-resize') {
        if (this.dragObject == null) {
          this.dragObject = event.target.parentNode;
          this.isDragging = false;
          this.isResizing = true;

          this.xOffset = event.clientX;
          this.yOffset = event.clientY;
          
          // console.log('resize', event.clientX, event.clientY, this.xOffset, this.yOffset);

          var boxes = document.getElementsByClassName("grid-box");
          for (let i = 0; i < boxes.length; i++) {
              boxes[i].style.zIndex = 2;
          }
          this.dragObject.style.zIndex = 3;
        }
      }

      

      


      // include 
      /*
      const clientWidth = event.target.clientWidth;
      const clientHeight = event.target.clientHeight;
      const offsetWidth = event.target.offsetWidth;
      const offsetHeight  = event.target.offsetHeight ;
      
      console.log(clientWidth, clientHeight, offsetWidth, offsetHeight);
      */
    },
    mouseUp (event) {
      console.log("up");
      event.preventDefault();
      if (this.dragObject != null) {
        /*
        console.log(this.$refs.gridBoxRef);
        // Call child function to resize the chart.
        const refs = this.$refs.gridBoxRef;
        for (let i = 0; i < refs.length; i++) {
          if (refs[i].$el.id === this.dragObject.id) {
            refs[i].resize();
            break;
          }
        }
        */
        this.dragObject = null;
        this.isDragging = false;
        this.isResizing = false;
        this.xOffset = 0;
        this.yOffset = 0;

        // TODO: only update moved box?? not all???
        this.$emit('box-changed', this.boxes);
      }
    },
    mouseMove (event) {
      event.preventDefault();

      // snap to grid
      // page size: 16: 9 or 4: 3
      // show gridlines
      let gridCell = 1;
      if (this.gridOptions.snapToGrid) {
        gridCell = 5;
      }

      const containerWidth = this.$el.clientWidth;
      const containerHeight = this.$el.clientHeight;

      let currentX = event.clientX - this.xOffset;
      let currentY = event.clientY - this.yOffset;

      if (this.isDragging) {
        
        currentX = Math.floor(currentX / gridCell) * gridCell;
        currentY = Math.floor(currentY / gridCell) * gridCell;

        // Add border width
        let boxWidth = parseInt(this.dragObject.style.width, 10) + 6;
        let boxHeight = parseInt(this.dragObject.style.height, 10) + 6;

        // console.log(currentX, currentY, boxWidth, boxHeight);

        // Restrict the move inside the container
        if (currentX < 0) {
          currentX = 0;
        }
        if (currentY < 0) {
          currentY = 0;
        }
        if (currentX + boxWidth > containerWidth) {
          currentX = containerWidth - boxWidth;
        }
        if (currentY + boxHeight > containerHeight) {
          currentY = containerHeight - boxHeight;
        }

        this.dragObject.style.left = currentX + 'px';
        this.dragObject.style.top = currentY + 'px';

        // Trigger resize in the child.
        const id = this.dragObject.id;
        const x = parseInt(this.dragObject.style.left, 10);
        const y = parseInt(this.dragObject.style.top, 10);
        const width = parseInt(this.dragObject.style.width, 10);
        const height = parseInt(this.dragObject.style.height, 10);

        const i = this.boxes.findIndex(x => 'grid-box-' + x.id === id);
        if (i !== -1) {
          this.boxes[i].x = x;
          this.boxes[i].y = y;
          this.boxes[i].width = width;
          this.boxes[i].height = height;
          console.log('resize - this.boxes', this.boxes[i]);
        } 
        
      } else if (this.isResizing) {
        
        // TODO: set mini weight and height
        const prevWidth = parseInt(this.dragObject.style.width, 10);
        const prevHeight = parseInt(this.dragObject.style.height, 10);
        // console.log('resize', prevWidth, prevHeight);


        //let WidthDelta = event.clientX - this.xOffset;
        //let heightDelta = event.clientY - this.yOffset;

        this.xOffset = event.clientX;
        this.yOffset = event.clientY;

        if (prevWidth + currentX <= 50) {
          currentX = 50 - prevWidth;
        }
        if (prevHeight + currentY <= 50) {
          currentY = 50 - prevHeight;
        }
        if (prevWidth + currentX > containerWidth) {
          currentX = containerWidth - prevWidth;
        }
        if (prevHeight + currentY > containerHeight) {
          currentY = containerHeight - currentY;
        }

        this.dragObject.style.width = Math.floor(prevWidth + currentX) + 'px';
        this.dragObject.style.height = Math.floor(prevHeight + currentY) + 'px';

        // Trigger resize in the child.
        const id = this.dragObject.id;
        const x = parseInt(this.dragObject.style.left, 10);
        const y = parseInt(this.dragObject.style.top, 10);
        const width = parseInt(this.dragObject.style.width, 10);
        const height = parseInt(this.dragObject.style.height, 10);

        const i = this.boxes.findIndex(x => 'grid-box-' + x.id === id);
        if (i !== -1) {
          this.boxes[i].x = x;
          this.boxes[i].y = y;
          this.boxes[i].width = width;
          this.boxes[i].height = height;
          console.log('resize - this.boxes', this.boxes[i]);
        } 
      }
    },
    onEdit(visualizationId) {
      this.$emit('edit-visualization', visualizationId);
    },
    onRemove(visualizationId) {
      this.$emit('remove-visualization', visualizationId);
    }
  }
}
</script>

<style>
.grid {
  position: relative;
  width: 500px;
  height: 500px;
  z-index: 1;
  background-color: #f1f1f1;
  border: 1px solid #4b4f56;
}
</style>
