<template>
  <div class="view-page">
    <div class="breadcrumb">
      Dashboard
    </div>
    <div class="view-content-full">
      <div>list of dashboard</div>

      <button class="button button1" @click="showDash = !showDash">
        <i class="fas fa-plus"></i> Add
      </button>

      <div>
        <div v-for="dash in dashboards" :key="dash.id">
          name: {{ dash.name }}
          <button class="button button1" @click="view(dash.id)"><i class="fas fa-desktop"></i></button>
          <button class="button button1" @click="edit(dash.id)"><i class="fas fa-edit"></i></button>
          <button class="button button1" @click="remove(dash.id)"><i class="fas fa-trash-alt"></i></button>
          <hr/>
        </div>
      </div>

    
      <transition name="slide">
        <div id="add-dashboard-panel" v-if="showDash">

          <div>
            <div>Name</div>
            <input type="text" v-model="name" />
          </div>

          <button class="button button1" @click="addDashboard">Add</button>

        </div>
      </transition>

    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Dashboard',
  data: () => ({
    name: '',
    width: 1280,
    height: 720,
    dashboards: [],
    showDash: false,
  }),
  created () {
    axios.get('ws/dashboard')
      .then(res => {          
        this.dashboards = res.data;
      });
  },
  methods: {
    addDashboard() {
      let dashboard = {};
      dashboard.name = this.name;
      dashboard.width = this.width;
      dashboard.height = this.height;
      axios.post('ws/dashboard', dashboard)
        .then(res => {    
          const id = res.data;
          console.log(id);
          this.name = '';
          this.$router.push({ path: `/dashboard/edit/${id}` });
        });
    },
    view (dashboardId) {
      this.$router.push({ path: `/dashboard/view/${dashboardId}` });
    },
    edit (dashboardId) {
      this.$router.push({ path: `/dashboard/edit/${dashboardId}` });
    },
    remove (dashboardId) {
      axios.delete('ws/dashboard/' + dashboardId);
    },
  }
}
</script>

<style scoped>

#add-dashboard-panel {
  position: fixed;
  left: 0;
  top: 100px;
  bottom: 0;
  width: 300px;
  border: 2px solid blue;
}

.slide-enter-active {
  transition: all .5s ease;
}

.slide-leave-active {
  transition: all .5s ease;
}

.slide-enter, .slide-leave-to {
  transform: translateX(-50px);
  opacity: 0;
}
</style>
