<template>
  <div class="view-page">
    <div class="view-content-full">
      <div class="{ tab-name: tab === 'indices' }" @click="switchTab('indices')">Indices</div>
      <div class="{ tab-name: tab === 'cluster' }" @click="switchTab('cluster')">Cluster</div>
      <div class="{ tab-name: tab === 'node' }" @click="switchTab('node')">Node</div>

      <div style="border-top: 2px solid black;">
        <div v-if="tab === 'cluster'">
          <table>
            <tr v-for="(value, key) in cluster" :key="key">
              <td>{{ key }}</td>
              <td>{{ value }}</td>
            </tr>
          </table>
        </div>

        <div v-if="tab === 'node'">
          <table>
            <thead>
              <tr>
                <th>Name</th>
                <th>IP</th>
                <th>Heap %</th>
                <th>Ram %</th>
                <th>CPU %</th>
                <th>Load 1m</th>
                <th>Load 5m</th>
                <th>Load 15m</th>
                <th>Role</th>
                <th>Master</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="d in node" :key="d.name">
                <td>{{ d.name }}</td>
                <td>{{ d.ip }}</td>
                <td>{{ d['heap.percent'] }}</td>
                <td>{{ d['ram.percent'] }}</td>
                <td>{{ d.cpu }}</td>
                <td>{{ d.load_1m }}</td>
                <td>{{ d.load_5m }}</td>
                <td>{{ d.load_15m }}</td>
                <td>{{ d['node.role'] }}</td>
                <td>{{ d.master }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        
        <div v-if="tab === 'indices'">
          <table>
            <thead>
              <tr>
                <th>Index</th>
                <th>Health</th>
                <th>Pri</th>
                <th>Rep</th>
                <th>Size</th>
                <th>Status</th>
                <th>Count</th>
                <th>Deleted</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="index in indices" :key="index">
                <td class="bold">{{ index.index }}</td>
                <td :style="healthStyle(index.health)"></td>
                <td>{{ index.pri }}</td>
                <td>{{ index.rep }}</td>
                <td>{{ index.size }}</td>
                <td>{{ index.status }}</td>
                <td>{{ index.count }}</td>
                <td>{{ index.deleted }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>


    </div>
  </div>

</template>

<script>
import axios from 'axios';

export default {
  name: 'System',
  data: () => ({
    tab: '',
    cluster: {},
    node: {},
    indices: []
  }),
  created () {
    this.switchTab('indices');
  },
  methods: {
    loadCluster () {
      axios.post('ws/es/query', {
        method: 'get',
        url: '/_cluster/health'
        })
        .then(res => {
          console.log(res.data);
          this.cluster = res.data;
        });
    },
    loadNode() {
      axios.post('ws/es/query', {
        method: 'get',
        url: '/_cat/nodes?format=json'
        })
        .then(res => {
          console.log(res.data);
          this.node = res.data;
        });
    },
    loadIndices() {
      const query = {
        method: 'get',
        url: '/_cat/indices?format=json'
      }

      axios.post('ws/es/query', query)
        .then(res => {
          this.indices = [];
          for (var i = 0; i < res.data.length; i++) {
            const d = res.data[i];
            const h =  {
              index: d.index,
              health: d.health,
              pri: d.pri,
              rep: d.rep,
              size: d['store.size'],
              status: d.status,
              count: d['docs.count'],
              deleted: d['docs.deleted']
            };
            this.indices.push(h);
          }
        });
    },
    switchTab (tab) {
      if (tab === 'cluster') {
        this.loadCluster();
      } else if (tab === 'node') {
        this.loadNode();
      } else if (tab === 'indices') {
        this.loadIndices();
      }
      this.tab = tab;
    },
    healthStyle (health) {
      let bgColor = '#ffa500';
      if (health === 'green') {
        bgColor = '#28a748';
      } else if (health === 'yellow') {
        bgColor = '#ffc108';
      } else if (health === 'red') {
        bgColor = '#dc3547';
      }
      return `background-color: ${bgColor};`;
    }
  }
}
</script>

