<template>
  <div>
    <div>
      <label>Start:</label>
      <datepicker v-model="startDate" format="yyyy-MM-dd" 
                  :disabledDates="disabledStartDates" 
                  @closed="onClosed"
                  input-class="time-input"
                  wrapper-class="inline-block"></datepicker>
    </div>

    <div>
      <label>End:</label>
      <datepicker v-model="endDate" format="yyyy-MM-dd" 
                  :disabledDates="disabledEndDates" 
                  @closed="onClosed"
                  input-class="time-input"
                  wrapper-class="inline-block"></datepicker>
    </div>
  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker';

export default {
  name: 'DateRangeFilter',
  components: {
    Datepicker,
  },
  props: {
    field: String,
    start: Date,
    end: Date,
  },
  data () {
    return {
      startDate: '',
      endDate: '',
      disabledStartDates: {},
      disabledEndDates: {}
    }
  },
  created () {
    this.startDate = this.date;
    this.endDate = this.date;
    this.changeDisabledDates();
  },
  /*
  watch: {
    start (date) {
      this.startDate = date;
    },
    end (date) {
      this.endDate = date;
    }
  },*/
  methods: {
    formatDate (date) {
      const d = new Date(date)
      let month = '' + (d.getMonth() + 1);
      let day = '' + d.getDate();
      const year = d.getFullYear();
      if (month.length < 2) {
        month = '0' + month;
      }
      if (day.length < 2) {
        day = '0' + day;
      }

      return [year, month, day].join('-');
    },
    changeDisabledDates () {
      this.$nextTick(() => {
        this.disabledStartDates = { to: this.start, from: this.endDate };
        this.disabledEndDates = { to: this.startDate, from: this.end };
      });
    },
    onClosed () {
      this.changeDisabledDates();
      this.$emit('date-changed', {
        start: this.startDate,
        end: this.endDate
      });
    }
  }
}
</script>

<style>
.num-range-input {
  width: 50px;
  outline: 0;
  padding: 3px 5px;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #1e9ead;
  background: transparent;
  font-size: 16px;
}
.time-input {
  outline: 0;
  width: 92px;
  padding: 3px 5px;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #1e9ead;
  background: transparent;
  font-size: 16px;
}

.vdp-datepicker__calendar .cell {
  vertical-align: middle;
  border: 1px solid #1e9ead;
  background-color: black;
  color: white;
}
</style>
