<template>
  <div class="view-page">
    <div class="breadcrumb">
      My Index
    </div>
    <div class="view-content-full">
      <div class="row">
        <div v-for="index in myIndices" :key="index.name" class="float-left" 
          style="margin-top: 10px; margin-left: 10px; border: 2px solid #2196F3; padding: 10px;">
          <div>{{ index.name }}</div>
          <div>{{ index.pattern }}</div>
          <div>
            <button class="button button1" @click="explore(index.id)"><i class="fas fa-glasses"></i></button>
            <button class="button button1" @click="edit(index.id)"><i class="fas fa-edit"></i></button>
            <button class="button button1" @click="remove(index.id)"><i class="fas fa-trash-alt"></i></button>
          </div>
        </div>
      </div>

      <button class="button button1" v-on:click="showAddDialog">
        <i class="fas fa-plus"></i> Add
      </button>
      <transition name="slide">
        <div id="add-index-panel" v-if="show">

          <div>
            <h2>Add Index</h2>
            <button class="button button1" @click="addIndex()">Add</button>
            <h3>Index Name</h3>
            <input type="text" class="form-input" v-model="name" />
            <h3>Index Pattern</h3>
            <input type="text" class="form-input" ref="pattern" v-model="pattern" @keyup="typePattern" @keydown="typePattern" />
            <ul>
              <li v-for="i in indices" :key="i">{{ i }}</li>
            </ul>

            
          </div>

        </div>
      </transition>

    </div>
  </div>
</template>

<script>
import axios from 'axios';
import * as poliApi from '@/api/PoliApi';

export default {
  name: 'Index',
  data: () => ({
    pattern: '*',
    name: '',
    indices: [],
    myIndices: [],
    show: false,
  }),
  created () {
    this.typePattern();
    this.loadMyIndices();
  },
  methods: {
    typePattern() {
      const pattern = this.pattern;
      let url = `/_cat/indices/${pattern}?format=json`;
      let query = {
        method: 'get',
        url: url
      }

      this.indices = [];
      axios.post('ws/es/query', query)
        .then(res => {          
          for (var i = 0; i < res.data.length; i++) {
            this.indices.push(res.data[i].index);
          }
        });
    },
    async loadMyIndices () {
      this.myIndices = await poliApi.getAllIndices();
    },
    explore (indexId) {
      this.$router.push({ path: `/index/explore/${indexId}` });
    },
    edit (indexId) {
      this.$router.push({ path: `/index/edit/${indexId}` });
    },
    async remove (indexId) {
      await poliApi.deleteIndex(indexId);
      await this.loadMyIndices();
    },
    showAddDialog() {
      this.show = !this.show;
      this.$nextTick(() => {
        this.$refs.pattern.setSelectionRange(0, 0);
        this.$refs.pattern.focus();
      });
    },
    addIndex () {
      if (this.pattern) {
        let index = {};
        index.name = this.name;
        index.pattern = this.pattern;

        axios.post('ws/index/', index)
          .then(res => {
            const indexId = res.data;
            this.$router.push({ path: `/index/edit/${indexId}` });
          });
      }
    }
  }
}
</script>

<style scoped>
#add-index-panel {
  position: fixed;
  right: 0;
  top: 44px;
  bottom: 0;
  width: 300px;
  border: 2px solid blue;
}

/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-enter-active {
  transition: all .5s ease;
}

.slide-leave-active {
  transition: all .5s ease;
}

.slide-enter, .slide-leave-to {
  transform: translateX(50px);
  opacity: 0;
}
</style>
