<template>
  <div style="min-width: 200px;">
    <button class="button button1" @click="applyFilter">Apply</button>
    <div v-for="d in selectors" :key="d.key" style="margin-top: 2px;">
      <label class="checkbox-container">
         
        <div class="progress-wrap">
          <div class="progress-bar" :style="{ width: d.width + '%' }"></div>
          <div class="progress-name">{{ d.key }}</div>
          <div class="progress-count">{{ d.count }}</div>
        </div>

        <input type="checkbox" v-model="d.selected">
        <span class="checkmark"></span>
      </label>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TextBarFilter',
  props: {
    data: Array
  },
  data () {
    return {
      selectors: []
    }
  },
  created () {
    console.log('TextBarFilter', this.data);
    let selectors = [];
    let max = 0;
    for (let i = 0; i < this.data.length; i++) {
      const count = this.data[i].count;
      if (count > max) {
        max = count;
      }
    }
    for (let i = 0; i < this.data.length; i++) {
      selectors.push({
        key: this.data[i].key,
        count: this.data[i].count,
        width: Math.floor(this.data[i].count * 100 / max),
        selected: false
      })
    }
    this.selectors = selectors;
    console.log('TextBarFilter', this.selectors);
  },
  methods: {
    applyFilter () {

    }
  }
}
</script>

<style>
.progress-wrap {
  background: #ddd;
  overflow: hidden;
  position: relative;
  height: 20px;
}

.progress-bar {
  background-color: #ffc107;
  height: 20px;
  left: 0;
  position: absolute;
  top: 0;
}

.progress-name {
  color: #007bff;
  font-size: 16px; 
  font-weight: bold;
  position: absolute;
  top: 0;
  left: 6px;
  z-index: 9;
}

.progress-count {
  color: #007bff;
  font-size: 16px; 
  font-weight: bold;
  position: absolute;
  top: 0;
  right: 0;
  background-color: orangered;
  height: 20px;
  z-index: 10;
}
</style>
