<template>
  <div class="view-page">
    <div class="row">
      <div class="breadcrumb float-left">Visualization: {{ name }}</div>

      <input type="text" v-model="name" />
      <div class="float-right"><button class="button button1" @click="save">Save</button></div>
      <div class="float-right"><button class="button button1" @click="backToDashboard">Back</button></div>

    </div>

    <div class="view-col-1 side-panel">
      <div class="side-panel-title">
        Visualizations
      </div>
      <div class="side-panel-content">
        <div class="row">
          <div class="vis-box" @click="type = 'table'">
            <i class="fas fa-table fa-2x"></i>
          </div>
          <div class="vis-box" @click="type = 'bar'">
            <i class="fas fa-chart-bar fa-2x"></i>
          </div>
          <div class="vis-box" @click="type = 'pie'">
            <i class="fas fa-chart-pie fa-2x"></i>
          </div>
          <div class="vis-box" @click="type = 'text'">
            <i class="fas fa-font fa-2x"></i>
          </div>
        </div>
      </div>

      <div class="side-panel-title">
        Drill down
      </div>
      <div class="side-panel-content">
        pie: -> key 
        table: -> column 
        text: -> link 
        bar: -> 
        line: ->
      </div>
      
      <div class="side-panel-title">
        Style
      </div>
      <div class="side-panel-content">
       chart style
      </div>


    </div>

    <div class="view-col-2 side-panel">

      <div class="side-panel-title">
        Index
      </div>
      <div class="side-panel-content">
        <select v-model="indexPattern" @change="onIndexChange" class="select">
          <option v-for="index in indices" :value="index.pattern" :key="index.id">
            {{ index.name }}
          </option>
        </select>
      </div>
      <div class="side-panel-title">
        Filter
      </div>
      <div class="side-panel-content">
        <select v-model="filterField" class="select">
          <option v-for="field in fields" :value="field" :key="field.id">
            {{ field.type }}, {{ field.name }}
          </option>
        </select>
        <div>
          <select v-model="filterOperator" class="select">
            <option v-for="operator in filterOperators" :value="operator" :key="operator">
              {{ operator }}
            </option>
          </select>
          <input type="text" v-model="filterValue"/>
          <button @click="addFilter">Add</button>
        </div>
        <div>
          <div v-for="(f, index) in filter" :key="index">
            {{ f.field.name }} {{ f.operator }} {{ f.value}}
            <button @click="deleteFilter(index)">delete</button>
          </div>
        </div>
      </div>

      <template v-if="type === 'pie'">
        <div class="title bold">
          Pie {{ pieChart.option.aggr }} {{ pieChart.option.value }} by {{ pieChart.option.key }}
        </div>
        
        <div class="title">Aggr</div>
        <div class="content">
          <select v-model="pieChart.option.aggr" @change="onPieAggrChange" class="select">
            <option v-for="option in pieChart.aggrs" :key="option">
              {{ option }}
            </option>
          </select>
        </div>

        <div class="title">Number</div>
        <div class="content">
          <select v-model="pieChart.option.value" class="select">
            <option v-for="field in pieChart.values" :value="field" :key="field">
              {{ field.type }}, {{ field.name }}
            </option>
          </select>
        </div>

        <div class="title">By value</div>
        <div class="content">
          <select v-model="pieChart.option.key" class="select">
            <option v-for="field in pieChart.keys" :value="field" :key="field">
              {{ field.type }}, {{ field.name }}
            </option>
          </select>
        </div>
      </template>

      <button class="button button1" @click="run">Run</button>
    </div>

    <div class="view-content-col-2">
      <v-chart :options="chartOptions" :auto-resize="true" />
    </div>
    
  </div>
</template>

<script>
import * as poliApi from '@/api/PoliApi';
import * as esApi from '@/api/EsApi';
import * as echartsApi from '@/api/EchartsApi';
import axios from 'axios';

export default {
  name: 'EditVisualization',
  data: () => ({
    visualizationId: 0,
    name: '',
    type: '',
    dashboardId: 0,
    indexPattern: '',
    style: '',
    option: {},
    filter: [],
    indices: [],
    fields: [],
    /**
     * filter
     */
    filterField: '',
    filterOperator: '',
    filterValue: '',
    /**
     * Chart options
     */
    pieChart: {
      option: {
        aggr: '',
        key: '',
        value: ''
      },
      aggrs: ['count', 'sum', 'avg'],
      keys: [],
      values: []
    },
    tableChart: {
      columns: []
    },
    barChart: {

    },
    textChart: {

    },
    chartOptions: {
    }
  }),
  created () {
    this.loadIndices();

    this.dashboardId = this.$route.params.dashboardId;
    this.visualizationId = this.$route.params.visualizationId;
    if (this.visualizationId === '0') {
      // New visualization
    } else {
      // Load existing visualization
      this.loadVisualization(this.visualizationId);
    }
    console.log('create pie', this.dashboardId, this.visualizationId);
  },
  computed: {
    filterOperators () {
      let operators = [];
      if (this.filterField.type === 'Text') {
        operators = ['contain', 'in'];
      } else if (this.filterField.type === 'Number') {
        operators = ['>=', '<=', '=='];
      } else if (this.filterField.type === 'Date') {
        operators = ['>=', '<='];
      }
      return operators;
    }
  },
  methods: {
    loadVisualization(vid) {
      axios.get('ws/visualization/' + vid)
        .then(res => {    
          const v = res.data;
          console.log('get vis', v);
          this.name = v.name;
          this.type = v.type;
          this.indexPattern = v.indexPattern;
          this.style = v.style || {};
          this.filter = v.filter || [];

          if (this.type === 'pie') {
            this.pieChart.option = v.option || {};
          }

          
          this.onIndexChange();
          this.run();

          console.log('aggr', this.pieChart.option);
        });
    },
    async loadIndices () {
      this.indices = await poliApi.getAllIndices();
    },
    async onIndexChange () {
      if (this.indexPattern) {
        this.index = await poliApi.getIndexByPattern(this.indexPattern);
        this.fields = this.index.fields;

        if (this.type === 'pie') {
          this.pieChart.keys = [];
          this.pieChart.values = [];
          for (let i = 0; i < this.fields.length; i++) {
            if (this.fields[i].type === 'Text') {
              this.pieChart.keys.push(this.fields[i]);
            }

            if (this.fields[i].type === 'Text' || this.fields[i].type === 'Number') {
              this.pieChart.values.push(this.fields[i]);
            }
          }

          this.onPieAggrChange();
        }
      }
    },
    onPieAggrChange () {
      this.pieChart.values = [];
      if (this.pieChart.aggr === 'count') {
        for (let i = 0; i < this.fields.length; i++) {
          if (this.fields[i].type === 'Text') {
            this.pieChart.values.push(this.fields[i]);
          }
        }
      }
      else if (this.pieChart.aggr === 'sum' || this.pieChart.aggr === 'avg') {
        for (let i = 0; i < this.fields.length; i++) {
          if (this.fields[i].type === 'Number') {
            this.pieChart.values.push(this.fields[i]);
          }
        }
      }
    },
    async run () {
      console.log('run', this.type);

      // handle filter
      const query = esApi.buildQuery(this.filter);
      console.log('filter', this.filter);
      console.log('query', query);

      if (this.type === 'pie') {
        console.log('pie - pattern:', this.indexPattern, 'esName', this.pieChart.option.key);

        // const field = this.fields.find(f => f.esName === this.pieChart.option.key);
        const result = await esApi.groupByTextCount(this.indexPattern, this.pieChart.option.key, query);
        
        let legend = [];
        let series = [];
        for (let i = 0; i < result.length; i++) {
          legend.push(result[i].key);
          series.push({
            name: result[i].key,
            value: result[i].count
          });
        }

        this.chartOptions = echartsApi.getPieOptions(legend, series);
        console.log(this.chartOptions);
      }
    },
    backToDashboard () {
      const dashboardId = this.dashboardId;;
      this.$router.push({ path: `/dashboard/edit/${dashboardId}` });
    },
    save () {
      
      let vis = {};
      vis.id = this.visualizationId;
      vis.dashboardId = this.dashboardId;
      vis.name = this.name;
      vis.type = this.type;
      vis.filter = this.filter;
      vis.indexPattern = this.indexPattern;
      if (this.type === 'pie') {
        vis.option = this.pieChart.option;
      }
      
      const dashboardId = this.dashboardId;
      if (this.visualizationId !== '0') {
        axios.put('ws/visualization', vis)
          .then(res => {    
            this.$router.push({ path: `/dashboard/edit/${dashboardId}` });
          });
      } else {
        axios.post('ws/visualization', vis)
          .then(res => {    
            this.$router.push({ path: `/dashboard/edit/${dashboardId}` });
          });
      }
      

    },
    addFilter() {
      const filter = {
        field: this.filterField,
        operator: this.filterOperator,
        value: this.filterValue
      }
      let isExist = false;
      for (let i = 0; i < this.filter.length; i++) {
        if (JSON.stringify(filter) === JSON.stringify(this.filter[i])) {
          isExist = true;
          break;  
        }
      }
      if (!isExist) {
        this.filter.push(filter);
        this.filterField = '';
        this.filterOperator = '';
        this.filterValue = '';
      }
    },
    deleteFilter(index) {
      this.filter.splice(index, 1);
    }
  }
}
</script>

<style>
.vis-box {
  width: 50px;
  height: 50px;
  text-align: center;
  border: 2px solid orange;
  margin: 10px;
  float: left;
}
</style>
