<template>
  <div class="view-page">
    <div>
      <div class="breadcrumb">Explore - {{ index.name }} {{ index.pattern }}</div>
    </div>

    <div class="view-col side-panel">
      <div class="side-panel-title">
        <div class="row">
          <div class="float-left bold">Fields</div>
          <div class="float-right"><i class="fas fa-exchange-alt fa-rotate-90"></i></div>
        </div>
      </div>
      <draggable v-model="columnFields" :options="{ group: 'fields' }" class="drag-panel">
        <div v-for="field in columnFields" :key="field.id" class="drag-row">
          <div class="row">
            <div class="field-type">
              <template v-if="field.type === 'Text'">
                Abc
              </template>
              <template v-else-if="field.type === 'Number'">
                #
              </template>
              <template v-else-if="field.type === 'Bool'">
                <i class="fas fa-calendar-alt"></i>
              </template>
              <template v-else-if="field.type === 'Date'">
                <i class="fas fa-calendar-alt"></i>
              </template>
              <template v-else>
                UN
              </template>
            </div>
            <div class="field-name">{{ field.name }}</div>
          </div>
        </div>
      </draggable>
    </div>

    <div class="view-content-col">
      <div class="table-panel">
        <table>
          <thead>
            <tr>
              <th v-for="field in columnFields" :key="field.id">
                {{ field.name }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td v-for="filter in filters" :key="filter.field" style="vertical-align: top;">
                <div style="max-height: 260px; overflow-y: auto;">
                  <template v-if="filter.type === 'Text'">
                    <TextBarFilter :data="filter.data"></TextBarFilter>
                  </template>
                  <template v-else-if="filter.type === 'Number'">
                    <div>min: {{ filter.data.min }}</div>
                    <div>max: {{ filter.data.max }}</div>
                  </template>
                  <template v-else-if="filter.type === 'Date'">
                    <div>min: {{ filter.data.min }}</div>
                    <div>max: {{ filter.data.max }}</div>
                  </template>
                  <template v-else-if="filter.type === 'Bool'">
                    <TextBarFilter :data="filter.data"></TextBarFilter>
                  </template>
                  <template v-else>
                    {{ filter.data }}
                  </template>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div>
        Rows per page:
        <select v-model="pageSize" @change="onPageSizeChange">
          <option>20</option>
          <option>50</option>
          <option>100</option>
        </select>

        {{ from }} - {{ toRecord }} of {{ totalSize }}
        <button class="button button1" @click="prevPage"><i class="fas fa-chevron-left"></i></button>
        <button class="button button1" @click="nextPage"><i class="fas fa-chevron-right"></i></button>
        <button class="button button1"><i class="fas fa-table"></i> Save as Table Vis</button>
        <button class="button button1"><i class="fas fa-file-csv"></i> CSV</button>
      </div>

      <div class="table-panel">
        <table>
          <thead>
            <tr>
              <th v-for="field in columnFields" :key="field.id" @click="sortBy(field)">
                {{ field.name }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(row, index) in table" :key="index">
              <td v-for="field in columnFields" :key="field.id">
                {{ row[field.esName] }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      
    </div>

  </div>
</template>

<script>
import draggable from 'vuedraggable';
import * as esApi from '@/api/EsApi';
import * as poliApi from '@/api/PoliApi';
import DateRangeFilter from '@/components/poli/DateRangeFilter';
import TextBarFilter from '@/components/poli/TextBarFilter';
import NumberRangeFilter from '@/components/poli/NumberRangeFilter';


const row = `[  
         {  
            "key":"account_number",
            "value":"25"
         },
         {  
            "key":"balance",
            "value":"40540"
         }]`;

export default {
  name: 'ExploreIndex',
  components: {
    draggable,
    DateRangeFilter,
    TextBarFilter,
    NumberRangeFilter
  },
  data: () => ({
    pageSize: 20,
    from: 0,
    totalSize: 0,
    fields: [],
    columnFields: [],
    /**
     * sortFields: [
     *  name: 'a',
     *  order: 'asc'
     * ]
     */
    sortFields: [],
    rows: [],
    index: {},
    filters: []
  }),
  created () {
    this.loadIndex();
  },
  computed: {
    toRecord () {
      const pageSize = Number(this.pageSize);
      const from = Number(this.from);
      if (from + pageSize >= this.totalSize) {
        return this.totalSize;
      }
      return from + pageSize;
    }
  },
  methods: {
    async loadIndex () {
      const indexId = this.$route.params.id;
      const index = await poliApi.getIndex(indexId);
      this.index = index;
      this.fields = index.fields;

      await this.loadTable();

      const filters = await esApi.getFilters(index.pattern, this.columnFields);
      this.filters = filters;
      
    },
    async loadTable() {
      let sort = [];
      for (let i = 0; i < this.sortFields.length; i++) {
        sort.push({
          [this.sortFields[i].name]: {
            "order": this.sortFields[i].order
          }
        });
      }
      
      const searchBody = {
        from: this.from,
        size: this.pageSize,
        sort: sort,
        // "query": query
      };
      console.log(searchBody);

      const searchResult = await esApi.searchTable(
        this.index.pattern, 
        searchBody
      );
      console.log(searchResult);

      this.totalSize = searchResult.size;
      const table = searchResult.table || [];
      const columns = searchResult.columns || [];
      
      this.columnFields = [];
      for (let i = 0; i < columns.length; i++) {
        for (let j = 0; j < this.fields.length; j++) {
          if (columns[i] === this.fields[j].esName) {
            this.columnFields.push(this.fields[j]);
            break;
          }
        }
      }
      console.log(this.columnFields);

      this.table = [];
      for (let i = 0; i < table.length; i++) {
        const row = table[i];
        const newRow = {};
        for (let j = 0; j < columns.length; j++) {
          for (let k = 0; k < row.length; k++) {
            if (columns[j] === row[k].key) {
              newRow[row[k].key] = row[k].value;
              break;
            }
          }
        }
        this.table.push(newRow);
      }
      console.log(this.table);
    },
    prevPage () {
      const pageSize = Number(this.pageSize);
      const from = Number(this.from);
      if (from - pageSize < 0) {
        this.from = 0;
      } else {
        this.from = from - pageSize;
      }
      this.loadTable();
    },
    nextPage () {
      const pageSize = Number(this.pageSize);
      const from = Number(this.from);
      if (from + pageSize >= this.totalSize) {
        return;
      }
      this.from = from + pageSize;
      this.loadTable();
    },
    onPageSizeChange () {
      this.loadTable();
    },
    sortBy (field) {
      let isUpdated = false;
      let esName = field.esName;
      if (field.type === 'Text') {
        esName = esName + '.keyword';
      }
      for (let i = 0; i < this.sortFields.length; i++) {
        if (this.sortFields[i].name === esName) {
          this.sortFields[i].order = this.sortFields[i].order === 'asc' ? 'desc' : 'asc';
          isUpdated = true;
          break;
        }
      }

      if (!isUpdated) {
        this.sortFields = [];
        // Only sort one field.
        this.sortFields.push({
          name: esName,
          order: 'asc'
        });
      }

      this.loadTable();
    },
    onDateFilterChange(dates) {
      console.log(dates.start, dates.end);
    }
  }
}
</script>

<style>
.view-col {
  position: absolute;
  top: 40px;
  left: 0;
  bottom: 0;
  width: 199px;
  overflow-y: auto;
  border-right: 1px solid #1e1e1e;
}

.view-content-col {
  position: absolute;
  top: 40px;
  left: 200px;
  right: 0;
  bottom: 0;
  background-color: #eeeeee;
  overflow: auto;
}

.drag-panel {
  min-height: 20px;
}

.drag-row {
  margin: 5px;
  padding: 5px;
  border-bottom: 1px solid grey;
  background-color: black;
  word-break: break-all;
  color: white;
  cursor: grab;
}

.field-type {
  display: inline-block;
  width: 26px;
  padding: 3px 5px;
  text-align: center;
  background-color: rgb(129, 104, 243);
}

.field-name {
  display: inline-block;
  color: red;
  padding: 3px 5px;
}
</style>
